<template>
    <div class='Mores'>
        <div class='title'>
            <h3>饿了么合并百度外卖 张旭豪发欢迎公开信：兄弟同心，其利断金！</h3>
            <p>2017-09-23</p>
        </div>
        <div class="content">
            <img src="/static/100.jpg" alt="">
            <p>8月24日，饿了么宣布合并百度外卖。合并完成后，百度外卖成为饿了么的全资子公司。百度外卖仍以独立的品牌和运营体系发展，包括管理层在内的人员架构保持不变。 </p>
            <p>创立3年，百度外卖在高端外卖市场积累了丰富、优质的用户和商户资源，人工智能配送技术一直走在行业最前沿。未来，饿了么将支持百度外卖发挥既往“品质外卖”的独特品牌优势，并投入流量、资金、配送人力等资源，助其拓展物流及代理商体系，继续打造高端本地生活平台和千亿级即时配送平台。 </p>
            <p>作为中国最大的本地生活平台，饿了么在交易规模和丰富度、配送运力、融资能力等方面具备优势。在外卖消费品质化、多元化趋势下，饿了么与百度外卖两大平台优势互补，整合各自在交易、配送和新零售等领域的资源，将产生显著协同效应，巩固新平台的领先地位。</p>
            <p>阿里巴巴为此次交易向饿了么提供了融资支持。百度通过换股方式也成为饿了么的股东。两家巨头拥有中国最顶级的互联网产品和技术实力，亦是具全球影响力的战略投资方。阿里巴巴和百度承诺，将给予新平台在流量入口、智能技术、金融保险等领域的全方位、更大力度的支持。 </p>
